<template>
  <div>
    <mu-appbar title="Edit">
      <mu-flat-button color="white" label="退出" slot="right" @click="backToAdmin"/>
    </mu-appbar>
    <mu-row class="edit-wrapper">
      <mu-col width="10" tablet="15" desktop="25"></mu-col>
      <mu-col width="80" tablet="70" desktop="50">
        <div class="image-wrapper">
          <mu-card>
            <mu-card-media>
              <img v-show="imgUrl" :src=imgUrl height="400"/>
              <mu-raised-button v-show="imgUrl" label="删除" @click="deletePhoto" secondary/>
              <div v-show="!imgUrl" class="icon-container">
                <mu-icon-button>
                  <mu-icon value="photo" :size="48"/>
                  <input type="file" class="file-button" @change="uploadFile($event)">
                </mu-icon-button>
              </div>
            </mu-card-media>
          </mu-card>
        </div>
        <mu-text-field v-model="title" hintText="在这里输入标题" fullWidth /><br/>
        <mu-text-field v-model="desc" hintText="在这里输入摘要" fullWidth /><br/>
        <mu-text-field v-model="category" hintText="在这里输入类别" fullWidth /><br/>
        <mu-text-field v-model="body" hintText="在这里输入正文" multiLine :rows="10" fullWidth /><br/>
        <mu-raised-button label="提交" class="demo-raised-button" @click="submitForm"/>
        <br/>
        <br/>
        <br/>
      </mu-col>
      <mu-col width="10" tablet="15" desktop="25"></mu-col>
    </mu-row>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      imgUrl: '',
      imgId: '',
      imgName: '',
      file: '',
      title: '',
      desc: '',
      category: '',
      body: ''
    }
  },
  methods: {
    backToAdmin () {
      this.$router.push({'name': 'Admin'})
    },
    uploadFile (event) {
      let _this = this
      _this.file = event.target.files[0]
      let param = new FormData()
      param.append('file', _this.file, _this.file.name)
      axios({
        url: '/photos',
        method: 'POST',
        headers: {'Authorization': window.sessionStorage.getItem('key'), 'Content-Type': 'multipart/form-data'},
        data: param
      }).then((response) => {
        _this.imgUrl = response.data.url
        _this.imgId = response.data.id
        _this.imgName = response.data.filename
      }).catch((error) => {
        console.log(error)
      })
    },
    deletePhoto () {
      let _this = this
      axios({
        url: '/photos/' + _this.imgName,
        method: 'DELETE',
        headers: {'Authorization': window.sessionStorage.getItem('key')}
      }).then((response) => {
        _this.imgUrl = ''
      }).catch((error) => {
        _this.imgUrl = ''
        console.log(error)
      })
    },
    submitForm () {
      let _this = this
      axios({
        url: '/posts',
        method: 'POST',
        headers: {'Authorization': window.sessionStorage.getItem('key'), 'Content-Type': 'application/json'},
        data: {
          'title': _this.title,
          'desc': _this.desc,
          'category': _this.category,
          'body': _this.body,
          'img_id': _this.imgId
        }
      }).then((response) => {
        this.$router.push({name: 'Admin'})
      }).catch((error) => {
        console.log(error)
      })
    }
  }
}
</script>

<style>
  .icon-container {
    display: block;
    height: 400px
  }
  .image-wrapper{
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .file-button{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    opacity: 0;
  }
</style>






